import React, { createContext, useState, useEffect } from "react";
import { useContext } from "react";
import { UrlContext } from "./UrlContext";
import { TokenContext } from "./TokenContext";
import axios from "axios";
export const ApiCall = createContext();

export const ApiCallProvider = ({ children }) => {
  const { url } = useContext(UrlContext);
  const { token } = useContext(TokenContext);

  const [userData, setUserData] = useState([]);
  const [postData, setPostData] = useState([]);
  const [eventData, setEventData] = useState([]);

  const config = {
    "Content-Type": "application/json",
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  /////////////////////////////////////////////////////////////////

  // Posts
  /////////////////////////////////////////////////////////////////

  const getPost = async () => {
    try {
      const response = await axios.get(`${url}/posts`, config);
      setPostData(response?.data);
      return response?.data.posts;
    } catch (err) {
      console.error("Error fetching User:", err);
      throw err;
    }
  };

  const readPost = async id => {
    try {
      const response = await axios.get(`${url}/posts/${id}`, config);
      return response.data;
    } catch (err) {
      console.error("Error fetching Post:", err);
      throw err;
    }
  };

  /////////////////////////////////////////////////////////////////

  // Events
  /////////////////////////////////////////////////////////////////

  const getEvent = async () => {
    try {
      const response = await axios.get(`${url}/events`, config);
      setEventData(response?.data);
      return response?.data;
    } catch (err) {
      console.error("Error fetching User:", err);
      throw err;
    }
  };

  const readEvent = async id => {
    try {
      const response = await axios.get(`${url}/events/${id}`, config);
      return response.data;
    } catch (err) {
      console.error("Error fetching Event:", err);
      throw err;
    }
  };

  useEffect(() => {
    const load = async () => {
      if (window.location !== "/login") {
        await getPost();

        await getEvent();
      }
    };

    load();
  }, []);

  /////////////////////////////////////////////////////////////////

  const contextValue = {
    //
    getPost,
    readPost,
    //
    getEvent,
    readEvent,
    //
    userData,
    postData,
    eventData,
  };

  return <ApiCall.Provider value={contextValue}>{children}</ApiCall.Provider>;
};
